'use client';

import { useState } from 'react';
import UserTable from './UserTable';
import UserView from './UserView';
import UserEdit from './UserEdit';

export default function UserTabs({ users }: { users: any[] }) {
  const [viewUser, setViewUser] = useState(null);
  const [editUser, setEditUser] = useState(null);

  return (
    <div className="relative">
      <UserTable
        users={users}
        onView={(user) => setViewUser(user)}
        onEdit={(user) => setEditUser(user)}
      />

      {viewUser && (
        <div className="absolute top-0 left-0 w-full h-full bg-white shadow-lg z-10">
          <UserView user={viewUser} onClose={() => setViewUser(null)} />
        </div>
      )}

      {editUser && (
        <div className="fixed top-0 right-0 w-1/2 h-full bg-white shadow-lg z-20">
          <UserEdit user={editUser} onClose={() => setEditUser(null)} />
        </div>
      )}
    </div>
  );
}
