export default function UserEdit({ user, onClose }: { user: any; onClose: () => void }) {
  return (
    <div className="p-6">
      <h3 className="text-xl font-bold mb-2">Editing {user.name}</h3>
      <form className="space-y-4">
        <input defaultValue={user.name} className="border px-3 py-2 rounded w-full" />
        <input defaultValue={user.email} className="border px-3 py-2 rounded w-full" />
        <select defaultValue={user.role} className="border px-3 py-2 rounded w-full">
          <option value="admin">Admin</option>
          <option value="user">User</option>
        </select>
        <button type="submit" className="bg-blue-600 text-white px-4 py-2 rounded">
          Save
        </button>
      </form>
      <button onClick={onClose} className="mt-4 text-red-600 hover:underline">
        Close
      </button>
    </div>
  );
}
