export default function UserView({ user, onClose }: { user: any; onClose: () => void }) {
  return (
    <div className="p-6">
      <h3 className="text-xl font-bold mb-2">Viewing {user.name}</h3>
      <p>Email: {user.email}</p>
      <p>Role: {user.role}</p>
      <button onClick={onClose} className="mt-4 text-red-600 hover:underline">
        Close
      </button>
    </div>
  );
}
